import gsap from "gsap";
import { useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";

// projectName : string,
// projectDesc : string,
// tagline : string,
// highlight : string,
// tools : array<string>,
// state : boolean
export default function ProjectDetailsOverlay({ projectName, projectDesc, projectTagline, highlight, tools, state }) {
    const containerRef = useRef(null);
    const barsRef = useRef([]); // To store references to the 5 bars

    useEffect(() => {
            const tl = gsap.timeline();

            tl.to(barsRef.current,
                {
                    yPercent: 100,  
                    duration: 0.8,
                    stagger: 0.1,  
                    ease: "power3.inOut"
                }
            );
            if(state)
                tl.play();
            else
                tl.reverse();
            
    }, [state]);

    const addToRefs = (el) => {
        if (el && !barsRef.current.includes(el)) {
            barsRef.current.push(el);
        }
    };

    return (
        <div ref={containerRef} className="fixed inset-0 w-screen h-screen z-99 flex pointer-events-none">
            
            {[...Array(5)].map((_, i) => (
                <div
                    key={i}
                    ref={addToRefs}
                    className="w-1/5 h-full bg-amber-800 relative pointer-events-auto -translate-y-full" 
                >
                </div>
            ))}

            <div className="absolute inset-0 z-10 flex flex-col justify-center items-center opacity-0 content">
                <h1 className="text-4xl font-bold">{projectName}</h1>
            </div>
        </div>
    );
}